<template>
 <div class="Realisations">
  <h1>Mes réalisations</h1>
  <hr>
  <div class="filters">
    <button class="filter" v-bind:class="{ active: currentFilter == 'All' }" v-on:click="setFilter('All')">All</button>
    <button class="filter" v-bind:class="{ active: currentFilter == 'Web' }" v-on:click= "setFilter('Web')">Web</button>
    <button class="filter" v-bind:class="{ active: currentFilter == 'Infographie' }" v-on:click= "setFilter('Infographie')">Infographie</button>
    <button class="filter" v-bind:class="{ active: currentFilter == 'Audiovisuel' }" v-on:click= "setFilter('Audiovisuel')">Audiovisuel</button>
    <button class="filter" v-on:click= "orderByYear">Par année</button>
  </div>

  <!-- GRILLE DES TRAVAUX -->
  <transition-group class="works" name="works">
     <div class="works-item" v-for="work in filteredWorks" v-bind:key="work.id">
      <div class="work-image-wrapper">
        <button type="button" id="show-modal" @click="onShowModal(work)">En savoir plus</button>
        <img class="work-image" v-bind:src="work.miniature">
      </div>
      <div class="text">
          <h2>{{work.title}}</h2>
      </div>
    </div>
  </transition-group>

  <!-- MODALE -->
  <transition name="translate">
  <div class="modale-backdrop" v-if="showModal" @close="showModal = false">
    <div class="modale">
      <div class="details">
        <div class="text">
          <h1>{{title}}</h1>
          <p><span>Année</span> : {{year}}</p>
          <p><span>Technos utilitées</span> : {{technos}}</p>
          <p><span>Description</span> : {{description}}</p>
          <p><span>Lien</span> : <a :href="link" target="_blank">{{link}}</a></p>
        </div>
        <div class="images">
          <img :src="image" width="" height="" alt="">
          <!--<h1>{{title}}</h1>
          <div class="overlay"></div>-->
        </div>
      </div>
      <button type="button" class="close" @click="showModal = false">X Fermer</button>
    </div>
  </div>
  </transition>

 <!--<div class="border"></div>-->
 </div>
</template>

<script>
import worksData from '@/components/works-data.js'
// import Modale from '@/components/modale/Modale.vue'

export default {
  name: 'Realisations',
  components: {
    // Modale
  },
  data () {
    return {
      currentFilter: 'All',
      showModal: false,
      works: worksData,
      name: '',
      year: '',
      technos: '',
      description: '',
      link: '',
      image: ''
    }
  },
  computed: {
    // display the works of the selected category
    filteredWorks: function () {
      const filter = this.currentFilter
      return this.works.filter(function (work) {
        return work.categories.indexOf(filter) !== -1
      })
    }
  },
  methods: {
    setFilter: function (filter) {
      this.currentFilter = filter
    },
    orderByYear: function () {
      this.works.sort((a, b) => a.year - b.year)
    },
    onShowModal: function (work) {
      this.showModal = true
      this.title = work.title
      this.year = work.year
      this.technos = work.technos
      this.description = work.description
      this.link = work.link
      this.image = work.image
    }
  }
}
</script>
